

.map {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 1000px;
    height: 800px;
    overflow: hidden;
}


.map-coordinates {
    display: inline-block;
    margin-left: 40px;
}


.btn {
    display: inline-block;
    margin-top: 40px;
    text-align: center;
    line-height: 40px;
    border: 1px solid black;
    width: 150px;
    cursor: pointer;
}

.btn:hover {
    background: black;
    color: white;
    border-color: white;
}


